.vision {
  position: relative;

  min-height: 691px;
  padding: 200px 0;

  background-color: $header-highlite-color;

  &__container {
    position: relative;
    z-index: 1;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    max-width: 715px;
    margin: 0 auto;

    text-align: center;
  }

  &__headline {
    font-size: 52px;
    font-weight: 600;

    color: $headline-color;

    margin-bottom: 24px;
  }

  &__text {
    @extend .middle-text;
    color: $text-color;
    
    margin-bottom: 64px;
  }
}
